import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { login, register } from "../../services/api/user";
import { setUserCookie } from "../../services/cookie";

const staticServerUri = process.env.REACT_APP_PATH || "";

//     : initial state    
const initialState = {
  email: null,
  loading: false, //    true,  :  , ,   false
  //error: null, //     error.message  .     .  extraReducer rejected 
  token: null
}

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    setEmail: (state, action) => {
      // action  email  state 
      state.email = "test email";
    },
    logout: (state, action) => {
      state.email = null;
      state.token = null;
    },
    setUserInfo: (state, action) => {
      state.email = action.payload.email;
      state.token = action.payload.token;
    }
  },
  extraReducers: (builder) => {
    /* loginRequest */
    // Promise pending()  : ,    
    builder.addCase(loginRequest.pending, (state, action) => {
      state.loading = true;
    });
    // Promise fulfilled() :   .
    builder.addCase(loginRequest.fulfilled, (state, action) => {
      const [tempEmail, tempToken] = [action.payload.email, action.payload.token]
      state.loading = false;
      state.email = tempEmail; // loginRequest return   
      state.token = tempToken;
      // add cookie - email, token 
      setUserCookie({email: tempEmail, token: tempToken})
      window.location.href = staticServerUri+"/"; // HomePage 
    });
    // Promise rejected():  
    builder.addCase(loginRequest.rejected, (state, action) => {
      state.loading = false;
      alert(action.error.message)
      //state.error = action.payload.error.message; //     .
    });

    /* registerRequest */
    builder.addCase(registerRequest.pending, (state, action) => {
      state.loading = true;
    });
    builder.addCase(registerRequest.fulfilled, (state, action) => {
      state.loading = false;
      window.location.href = staticServerUri+"/"; // HomePage 
      alert(" .")
    });
    builder.addCase(registerRequest.rejected, (state, action) => {
      state.loading = false;
      alert(action.error.message)
    });
  }
})

export const loginRequest = createAsyncThunk(
  "user/login",
  async (data) => {
    const {email, password} = data;
    const response = await login({email, password});
    //return response.data
    return {
      email: email,
      token: response.headers.authorization,
    }; //   .
  }
);
export const registerRequest = createAsyncThunk(
  "user/join",
  async (data) => {
    const {email, password, username} = data;
    const response = await register({email, password, username});
    return response.data
  }
);

// userSlice reducer export (reducer .actions .)
export const { logout, setUserInfo, setEmail } = userSlice.actions
// src/store/index.js export default userReducer   userSlice  export
export default userSlice.reducer;