import Container from "../atoms/Container";
import InputGroup from "../molecules/InputGroup";
import Button from "../atoms/Button";
import useInput from "../../hooks/useInput";
//import { login } from "../../services/api"; //loadingRequest 
import { loginRequest } from "../../store/slices/userSlice";
import { useDispatch } from "react-redux";
import { emailValidCheck, pwValidCheck } from "../../utils/validationCheck";
import { useState } from "react";
import { Link } from "react-router-dom";

const staticServerUri = process.env.REACT_APP_PATH || "";

const LoginForm = () => {
  //   store  : dispatch   useDispatch;
  const dispatch = useDispatch();
  //   store   : useSelector  
  //   state       state
  // const email = useSelector((state) => state.user);

  const { value, handleOnChange } = useInput({
    email: "",
    password: "",
  });

  //RegisterForm validation  ,  
  const [validation, setValidation] = useState({
    email: true,
    password: true,
  });
  const handleLogin = () => {
    const isEmailValid = emailValidCheck(value.email);
    const isPwValid = pwValidCheck(value.password);

    setValidation({
      email: isEmailValid,
      password: isPwValid,
    });

    if (isEmailValid && isPwValid) {
      //   ,  
      dispatch(
        loginRequest({
          email: value.email,
          password: value.password,
        })
      );
    }
  };

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleLogin();
    }
  };

  //redux-thunk     ,   onClick dispatch(loginRequest) 
  // const loginReq = () => {
  //   login({
  //     email: value.email,
  //     password: value.password,
  //   })
  //     .then((res) => {
  //       //console.log(res);
  //       // dispatch  store email 
  //       dispatch(
  //         setEmail({
  //           email: value.email,
  //         })
  //       );
  //     })
  //     .catch((err) => {
  //       console.log("Error", err);
  //     });
  // };
  const [isFocus, setIsFocus] = useState([false, false]);
  return (
    <Container
      className="border-neutral-300 border px-16 py-14 w-[560px] min-w-fit h-full my-10 mx-auto"
      onKeyDown={handleKeyDown}
    >
      <div className="text-center font-normal text-3xl mx-auto my-10">
        
      </div>
      <InputGroup
        id={"email"}
        type={"email"}
        name={"email"}
        placeholder={"()"}
        //label={""}
        value={value.email}
        onChange={handleOnChange}
        ///
        className={
          isFocus[0]
            ? "border-b-2 border-neutral-500 m-5 mb-2"
            : "border-b-2 border-neutral-300 m-5 mb-2"
        }
        inputClass={"focus:outline-0 focus:bt-black w-full m-3"}
        onFocus={() => {
          setIsFocus([true, isFocus[1]]);
        }}
        onBlur={() => {
          setIsFocus([false, isFocus[1]]);
        }}
      />
      <div className="m-2 text-red-500 pl-3">
        {validation.email ? "" : "  ."}
      </div>
      <InputGroup
        id={"password"}
        type={"password"}
        name={"password"}
        placeholder={""}
        //label={""}
        value={value.password}
        onChange={handleOnChange}
        ///
        className={
          isFocus[1]
            ? "border-b-2 border-neutral-500 m-5 mb-2"
            : "border-b-2 border-neutral-300 m-5 mb-2"
        }
        inputClass={"focus:outline-0 focus:bt-black w-full m-3"}
        onFocus={() => {
          setIsFocus([isFocus[0], true]);
        }}
        onBlur={() => {
          setIsFocus([isFocus[0], false]);
        }}
      />
      <div className="m-2 text-red-500 pl-3">
        {validation.password
          ? ""
          : ", ,  , 8 20  ."}
      </div>
      <Button
        className={
          "block w-full h-12 mt-10 rounded bg-yellow-300 hover:bg-yellow-400"
        }
        onClick={handleLogin}
      >
        
      </Button>
      <Link
        to={staticServerUri + "/signup"}
        className=" inline-block my-6 mx-3 ml-[350px]"
      >
        
      </Link>
    </Container>
  );
};
export default LoginForm;
