const Input = ({
  type,
  name,
  value,
  onChange,
  placeholder,
  className,
  id,
  onFocus,
  onBlur,
}) => {
  return (
    <input
      type={type}
      value={value}
      onChange={onChange}
      placeholder={placeholder}
      className={className}
      id={id} // id label      
      name={name} // name event.target.name     
      onFocus={onFocus}
      onBlur={onBlur}
    />
  );
};
export default Input;
