import {instance} from "./index"

//   
// 1. register 
export const register = (data) => { // data  {email, password, username}       .    
  const { email, password, username } = data; // data email, username   (  email, username  ) api     
  return instance.post("/join", {
    email,
    password,
    username,
  });
}

// 2. login 
export const login = (data) => { 
  const { email, password } = data;
  return instance.post("/login", {
    email,
    password,
  });
}