import { useParams } from "react-router-dom";
import Loader from "../components/atoms/Loader";
import { useQuery } from "react-query";
import { getProductById } from "../services/api/product";
import ProductDetailTemplate from "../components/templates/ProductDetailTemplate";
import ErrorTypo from "../components/atoms/ErrorTypo";

const ProductDetailPage = () => {
  // const dispatch = useDispatch(); // react-query  disable
  // useParams   ex: ...URL/user/1 user pathname, 1 parameter
  const { id } = useParams(); // ! useParams    string.    
  // const parsedId = parseInt(id, 10); // 10  //        

  /* 
            ? => 
  ?      detail  
           .
  const {loading, error, detail} = useSelector((state) => state.detail); 
  */
  /*     ? => useSWR  react-query   */
  // react-query   
  // const loading = useSelector((state) => state.detail.loading);
  // const error = useSelector((state) => state.detail.error);
  // const detail = useSelector((state) => state.detail.detail);

  // react-query   
  const { data, error, isLoading } = useQuery(`product/${id}`, () =>
    getProductById(id)
  ); // , API  

  // react-query   
  // useEffect(() => {
  //   dispatch(getDetail(id));
  // }, [dispatch, id]);

  // props drilling   redux  context api    .(product)

  const product = data?.data?.response;
  // product        .
  // 1. Typescript interface, type  
  // 2.  : data   , validate function

  const validate = () => {
    if (!product) {
      return false;
    }
    const requiredKeys = ["id", "productName"];
    const keys = Object.keys(product); // Object   keys  product Key  
    for (let i = 0; i < requiredKeys.length; i++) {
      const requiredKey = requiredKeys[i];
      if (!keys.includes(requiredKey)) {
        alert(`product  ${requiredKeys}  .`);
        return false;
      }
    }
    return true;
  };

  if (isLoading) return <Loader />;
  else if (error) return <ErrorTypo />;
  else if (validate() === false) return <ErrorTypo />;
  else return <ProductDetailTemplate product={product} />;
};

export default ProductDetailPage;
