import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { fetchProducts } from "../../services/api/product";
import _ from "lodash";

const initialState = {
  products: [],
  loading: false,
  error: null, // error exist: {message, status}
  isEnd: false,
}

const productsSlice = createSlice({
  name: "products",
  initialState,
  reducers: {

  },
  extraReducers: (builder) => {
    builder.addCase(getProducts.pending, (state, action) => {
      state.loading = true;
    });
    // Promise.resolve()
    builder.addCase(getProducts.fulfilled, (state, action) => {
      // action.payload.response  10   
      // 10      
      if (action.payload.response.length === 0){
        state.isEnd = true;
      }
      state.loading = false;
      
      // response  products   - lodash
      //             
      // // action.payload = {success, response, error}
      state.products = _.uniqBy([...state.products, ...action.payload.response], "id"); // id  unique  

      state.error = action.payload.error;
    });
    // Promise.reject()
    builder.addCase(getProducts.rejected, (state, action) => {
      state.loading = false;
      console.log(action)
      state.error = action.payload.error; // error exist: {message, status}
    });
  }
  
})

export const getProducts = createAsyncThunk(
  "products/getProducts",
  async (page) => {
    const response = await fetchProducts(page);
    // API   
    // response.data = {success: , response: [], error: }
    return response.data; // action.payload  .
  }
)

export default productsSlice.reducer;